.product-container{
    background-color: #F0FFFF;
    -webkit-box-shadow: 10px 10px 22px -4px rgba(186,178,181,0.62);
            box-shadow: 10px 10px 22px -4px rgba(186,178,181,0.62);
    margin: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 30%;
    height: 80%;
}
.img{
    width:  100%;
    height: 400px;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: cover;
}

.product-name{
    -ms-flex-item-align: start;
        align-self: flex-start;
    font-size: 300%;
    color: #606060;
}

.product-price{
    -ms-flex-item-align: end;
        align-self: flex-end;
    font-size: 200%;
    -ms-flex-item-align: center;
        align-self: center;
    color: #606060;

}

.product-description{
    font-size: 200%;
    color: #909090;
}

.button-container{
    -ms-flex-item-align: center;
        align-self: center;
}
.add-button{
    color: #606060;
    height: 100px;
    font-size: 300%;
    width: 100%;
    display: none;
    border: none;
    -webkit-animation: button-ani 2s linear;
            animation: button-ani 2s linear;
}

.product-container:hover > .add-button{
    display: inline-block;
    -webkit-animation-play-state: running;
            animation-play-state: running;
}
.product-container:hover > .product-price{
    display: none;
}

@-webkit-keyframes button-ani{
    0% {
        opacity: 0;
    }

    100%{
        opacity: 1;
        display: inline-block;
    }
}

@keyframes button-ani{
    0% {
        opacity: 0;
    }

    100%{
        opacity: 1;
        display: inline-block;
    }
}


